function resize() {
  // set the canvas size
  canvas.style.width = innerWidth + 'px';
  canvas.style.height = innerHeight + 'px';

  // calculate the internal pixel dimensions of the canvas
  canvas.width = innerWidth * devicePixelRatio;
  canvas.height = innerHeight * devicePixelRatio;

  ctx.scale(devicePixelRatio, devicePixelRatio);

  padWidth = (innerWidth - 2 * MARGIN) / 13;
  padHeight = (innerHeight - 2 * MARGIN) / 8;

  buttonWidth = padWidth * 1.5;
  buttonHeight = padHeight / 1.5;
}

window.addEventListener('resize', resize);
resize();

const NUM_VOICES = 8;

const STATUS_LINGER_TIME_MS = 2_000;
