// ----- interface w/ worklet -----

class Voice {
  readonly params = new Float32Array<any>(new SharedArrayBuffer(128 * 4));

  constructor(
    readonly synth: Synth,
    readonly channel: number,
  ) { }

  note = -1;

  noteOn(note: number, velocity: number) {
    if (this.isOn) {
      this.noteOff(this.note, 127);
    }
    this.note = note;
    this.synth.sendToWorklet({
      command: 'process midi message',
      data: midi.noteOn(this.channel, note, velocity),
    });
  }

  noteOff(note: number, velocity: number) {
    this.note = -1;
    this.synth.sendToWorklet({
      command: 'process midi message',
      data: midi.noteOff(this.channel, note, velocity),
    });
  }

  get isOn() {
    return this.note !== -1;
  }

  get freq() {
    return toFrequency(this.note) * Math.pow(2, this.params[0] / 12);
  }

  // param getters and setters

  set glide(value: number) {
    this.params[0] = value;
  }

  get glide() {
    return this.params[0];
  }

  set slide(value: number) {
    this.params[1] = value;
  }

  get slide() {
    return this.params[1];
  }

  set pressure(value: number) {
    this.params[2] = value;
  }

  get pressure() {
    return this.params[2];
  }

  set paramA(value: number) {
    this.params[3] = value;
  }

  get paramA() {
    return this.params[3];
  }

  set paramB(value: number) {
    this.params[4] = value;
  }

  get paramB() {
    return this.params[4];
  }

  set paramC(value: number) {
    this.params[5] = value;
  }

  get paramC() {
    return this.params[5];
  }

  set paramD(value: number) {
    this.params[6] = value;
  }

  get paramD() {
    return this.params[6];
  }

  set gain(value: number) {
    this.params[7] = value;
  }

  get gain() {
    return this.params[7];
  }
}

class Synth {
  patch: Patch;
  mode: Mode = 'mpe';
  worklet: AudioWorkletNode | null = null;
  voices: Voice[] = [];
  octave = 0;
  analyzer: AnalyserNode | null = null;
  timeDomainData: Uint8Array<any> | null = null;

  constructor(patchName: string = 'pwm') {
    this.patch = patchLibrary.find((p) => p.name === patchName)!;
    if (!this.patch) {
      throw new Error('unknown patch name! ' + patchName);
    }

    for (let ch = 0; ch < NUM_VOICES; ch++) {
      this.voices.push(new Voice(this, ch));
    }
  }

  init() {
    this.worklet = new AudioWorkletNode(context, 'msynth');
    this.sendToWorklet({
      command: 'init',
      voiceParams: this.voices.map((voice) => voice.params.buffer),
    });

    this.analyzer = context.createAnalyser();
    this.analyzer.fftSize = 32768;
    const bufferLength = this.analyzer.frequencyBinCount;
    this.timeDomainData = new Uint8Array(bufferLength);

    // Important!
    this.worklet.channelInterpretation = 'discrete';
    this.worklet.channelCount = 2;
    this.worklet.channelCountMode = 'explicit';
    this.worklet.connect(this.analyzer).connect(context.destination);
    this.worklet.port.onmessage = (msg) => onMessageFromWorklet(msg.data);

    this.loadPatch(this.patch);
  }

  makeActive() {
    synth = this;
    this.loadPatchInEditor();
  }

  getTimeDomainData(): Uint8Array | null {
    if (!this.analyzer || !this.timeDomainData) {
      return null;
    }

    this.analyzer.getByteTimeDomainData(this.timeDomainData!);
    return this.timeDomainData;
  }

  sendToWorklet(message: MessageToWorklet) {
    this.worklet?.port.postMessage(message);
  }

  loadPatch(patch: Patch, loadInEditor = true) {
    this.patch = patch;
    this.sendToWorklet({
      command: 'load patch',
      code: patch.code,
      mono: patch.options?.mono ?? false,
    });
    this.setParam(3, patch.options?.paramA ?? 0);
    this.setParam(4, patch.options?.paramB ?? 0);
    this.setParam(5, patch.options?.paramC ?? 0);
    this.setParam(6, patch.options?.paramD ?? 0);
    this.setParam(7, 0.2); // gain
    this.octave = patch.options?.octave ?? 0;
    this.mode = patch.options?.mode ?? 'mpe';

    if (synth === this && loadInEditor) {
      this.loadPatchInEditor();
    }
  }

  loadPatchInEditor() {
    editor.setContents(this.patch.code);
  }

  savePatch() {
    this.patch.code = editor.view.state.doc.toString();
    this.loadPatch(this.patch, false);
  }

  grabVoice() {
    if (this.patch.options?.mono) {
      return this.voices.find((v) => v.channel === 0)!;
    }

    const fingers = [...fingersById.values()];
    const freeVoice = this.voices.findLast((v) => !fingers.some((f) => f.voice === v));
    const voice = freeVoice ?? this.voices.at(-1)!; // if there's no free voice, steal the least recently claimed
    this.voices.splice(this.voices.indexOf(voice), 1);
    this.voices.unshift(voice);
    return voice;
  }

  setParam(n: number, value: number) {
    for (const voice of this.voices) {
      voice.params[n] = value;
    }
  }

  lnf = 220;

  getLowestNoteFreq() {
    let lnf = Infinity;
    for (const voice of this.voices) {
      if (voice.isOn && voice.freq < lnf) {
        lnf = voice.freq;
      }
    }
    if (Number.isFinite(lnf)) {
      this.lnf = lnf;
    }
    return this.lnf;
  }

  // ----- UI stuff -----

  controls(oscilloscope: Oscilloscope) {
    const patchSelector = new EmbeddedSelectorThing(
      oscilloscope,
      'top',
      'left',
      patchLibrary,
      () => this.patch,
      (patch: Patch) => {
        this.loadPatch(patch);
      },
      (patch) => patch.name,
      () => synth === this,
    );
    const modeSelector = new EmbeddedSelectorThing<Mode>(
      oscilloscope,
      'top',
      'right',
      ['mpe', 'string', 'piano', 'strum'],
      () => this.mode,
      (mode) => {
        this.mode = mode;
      },
      (mode) => mode,
      () => synth === this,
    );
    const octaveSelector = new EmbeddedSelectorThing(
      oscilloscope,
      'bottom',
      'right',
      [2, 1, 0, -1, -2],
      () => this.octave,
      (value: number) => {
        this.octave = value;
      },
      (octave) => 'octave ' + (octave > 0 ? '+' : '') + octave,
      () => synth === this,
      true,
    );
    const paramABJoystick = new ParamJoystick(this, 3, 4, '(a,b)', () => oscilloscope.rightX);
    const paramCDJoystick = new ParamJoystick(this, 5, 6, '(c,d)', paramABJoystick);
    const volKnob = new ParamKnob(this, 7, 'vol', paramCDJoystick);
    return [patchSelector, modeSelector, octaveSelector, paramABJoystick, paramCDJoystick, volKnob];
  }
}

const synth1 = new Synth('rebel yell');
const synth2 = new Synth('stranger things');
let synth = synth1;

// ---- playing UI ----

const LOW_B = 47;
const PERFECT_4TH = 5;

const RECTANGLE_RADIUS = 5;

const EDITOR_HEIGHT_IN_PADS = 3;
let editingPatch = false;
let padRowOffset = 0;

// TODO: make Pad a Thing
class Pad {
  constructor(
    public col: number,
    public row: number,
    public noteName: string,
  ) { }

  get note() {
    return LOW_B + 12 * synth.octave + PERFECT_4TH * (7 - this.row) + this.col;
  }

  get x() {
    return MARGIN + this.col * padWidth;
  }

  get y() {
    return MARGIN + (this.row + padRowOffset) * padHeight;
  }

  get rightX() {
    return this.x + padWidth;
  }

  get bottomY() {
    return this.y + padHeight;
  }

  get centerX() {
    return this.x + padWidth / 2;
  }

  get centerY() {
    return this.y + padHeight / 2;
  }

  contains(px: number, py: number) {
    return this.x <= px && px <= this.rightX && this.y <= py && py <= this.bottomY;
  }

  isBeingPlayedBySequencer = false;

  render() {
    ctx.beginPath();
    ctx.lineWidth = 3;
    ctx.strokeStyle = '#efefef';
    ctx.roundRect(this.x, this.y, padWidth, padHeight, RECTANGLE_RADIUS);
    ctx.stroke();

    if (synth.mode === 'strum' && this.col >= LEFTMOST_STRUM_COL) {
      ctx.beginPath();
      ctx.fillStyle = DARKER_BACKGROUND_COLOR_ACTIVE;
      ctx.roundRect(this.x, this.y, padWidth, padHeight, RECTANGLE_RADIUS);
      ctx.fill();
      return;
    }

    ctx.beginPath();
    ctx.fillStyle =
      this.noteName === '' ? DARK_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR_ACTIVE;
    ctx.roundRect(this.x, this.y, padWidth, padHeight, RECTANGLE_RADIUS);
    ctx.fill();

    if (synth === synth2 && this.isBeingPlayedBySequencer) {
      ctx.beginPath();
      ctx.fillStyle =
        this.noteName === ''
          ? PAD_BLACK_KEY_PLAYED_BY_SEQ_COLOR
          : PAD_WHITE_KEY_PLAYED_BY_SEQ_COLOR;
      ctx.roundRect(this.x + 20, this.y + 20, padWidth - 40, padHeight - 40, RECTANGLE_RADIUS);
      ctx.fill();
    }

    if (this.noteName !== '') {
      ctx.globalAlpha = 0.5;
      ctx.beginPath();
      ctx.font = PAD_FONT;
      ctx.fillStyle = TEXT_COLOR_ACTIVE;
      ctx.fillText(this.noteName, this.x + 4, this.y + 16);
      ctx.fill();
      ctx.globalAlpha = 1;
    }
  }
}

const NUM_COLS = 13;
const NUM_ROWS = 6;
const FIRST_ROW = 2;

const pads: Pad[] = [];
const noteNames = ['C', '', 'D', '', 'E', 'F', '', 'G', '', 'A', '', 'B'];
let noteNameIdxOffset = 0;
for (let row = FIRST_ROW; row < FIRST_ROW + NUM_ROWS; row++) {
  for (let col = 0; col < NUM_COLS; col++) {
    pads.push(new Pad(col, row, noteNames[(noteNameIdxOffset + col) % 12]));
  }
  noteNameIdxOffset += 7;
}

const fingersById = new Map<number, Finger>();

abstract class Finger {
  static add(id: number, x0: number, y0: number, pressure: number, _synth = synth) {
    switch (_synth.mode) {
      case 'piano':
        return new PianoFinger(id, x0, y0, pressure, _synth);
      case 'mpe':
        return new MpeFinger(id, x0, y0, pressure, _synth);
      case 'string':
        return new StringFinger(id, x0, y0, pressure, _synth);
      case 'strum': {
        const pad = pads.find((pad) => pad.contains(x0, y0));
        return pad && pad.col >= LEFTMOST_STRUM_COL
          ? new PickFinger(id, x0, y0, pressure, _synth)
          : new FretFinger(id, x0, y0, pressure, _synth);
      }
      default:
        throw new Error('unknown mode! ' + _synth.mode);
    }
  }

  static updateQuantizationOffsets() {
    for (const finger of fingersById.values()) {
      if (finger instanceof MpeFinger || finger instanceof StringFinger) {
        finger.updateQuantizationOffset();
      }
    }
  }

  x: number;
  y: number;
  quantizationOffset = 0;
  voice: Voice | null = null;
  pad: Pad | null = null;
  readonly speedTracker = new SpeedTracker();

  constructor(
    readonly id: number,
    readonly x0: number,
    readonly y0: number,
    readonly pressure: number,
    readonly synth: Synth,
  ) {
    this.x = x0;
    this.y = y0;
    fingersById.set(id, this);
    this.move(x0, y0, pressure);
  }

  abstract move(x: number, y: number, pressure: number): void;
  abstract lift(): void;
  abstract render(): void;

  lastT = 0;

  updateQuantizationOffset() {
    const pad = pads.find((p) => p.row === this.pad?.row && p.contains(this.x, this.y));

    if (this.speedTracker.speed > 0.025 || !this.pad || !pad) {
      this.lastT = performance.now();
      return;
    }

    const now = performance.now();
    const dt = now - this.lastT;

    const adjustmentSpeed = 0.005; // used to be 0.002
    const f = adjustmentSpeed * dt;
    const wantedQuantizationOffset = (pad.centerX - this.x) / padWidth;
    this.quantizationOffset = f * wantedQuantizationOffset + (1 - f) * this.quantizationOffset;
    this.updateGlide();

    this.lastT = now;
  }

  updateGlide() {
    this.voice!.glide = (this.x - this.pad!.centerX) / padWidth + this.quantizationOffset;
  }
}

class PianoFinger extends Finger {
  move(x: number, y: number, pressure: number) {
    const newPad = pads.find((pad) => pad.contains(x, y)) ?? this.pad;
    const changedPads = newPad !== this.pad;

    if (this.pad && changedPads) {
      this.voice!.noteOff(this.pad.note, 127);
      this.voice = null;
    }

    if (newPad && changedPads) {
      this.voice = this.synth.grabVoice();
    }

    this.pad = newPad;

    if (this.pad) {
      this.voice!.glide = 0;
      this.voice!.slide = clamp((this.pad.centerY - y) / (padHeight / 2), -1, 1);
      this.voice!.pressure = pressure;
      if (changedPads) {
        this.voice!.noteOn(this.pad.note, 127);
      }
    }
  }

  lift() {
    this.voice?.noteOff(this.pad!.note, 127);
  }

  render() {
    renderMpeFinger(this.pad, this.voice);
  }
}

class MpeFinger extends Finger {
  move(x: number, y: number, pressure: number) {
    this.x = x;
    this.y = y;
    this.speedTracker.addPos(x);

    const newPad = pads.find((pad) => pad.row !== this.pad?.row && pad.contains(x, y)) ?? this.pad;
    const changedPads = newPad !== this.pad;

    if (this.pad && changedPads) {
      this.voice!.noteOff(this.pad.note, 127);
      this.voice = null;
    }

    if (newPad && changedPads) {
      this.voice = this.synth.grabVoice();
      this.quantizationOffset = (newPad.centerX - x) / padWidth;
      // console.log(this.id + ': set initial q offset to', this.quantizationOffset);
    }

    this.pad = newPad;

    if (this.pad) {
      this.updateGlide();
      this.voice!.slide = clamp((this.pad.centerY - y) / (padHeight / 2), -1, 1);
      this.voice!.pressure = pressure;
      if (changedPads) {
        this.voice!.noteOn(this.pad.note, 127);
      }
    }
  }

  lift() {
    this.voice?.noteOff(this.pad!.note, 127);
  }

  render() {
    renderMpeFinger(this.pad, this.voice);
  }
}

class StringFinger extends Finger {
  pressure = 0;

  move(x: number, y: number, pressure: number) {
    this.x = x;
    this.y = y;
    this.pressure = pressure;
    this.speedTracker.addPos(x);

    const newPad = pads.find((pad) => pad.row !== this.pad?.row && pad.contains(x, y));
    const firstInRow = newPad ? firstPadIn(newPad.row) : this.pad;
    const changedPads = firstInRow !== this.pad;

    if (this.pad && changedPads) {
      const row = this.pad.row;
      this.pad = null;
      this.updateRow(row); // this will do a note off if appropriate
      this.voice = null;
    }

    if (firstInRow && changedPads) {
      this.voice = this.synth.voices[firstInRow.row];
      this.quantizationOffset = (newPad!.centerX - x) / padWidth;
    }

    this.pad = firstInRow;

    if (this.pad) {
      this.updateRow(this.pad.row);
    }
  }

  updateRow(row: number) {
    const voice = this.synth.voices[row];
    const pad = firstPadIn(row);
    const highestFinger: StringFinger | null = this.getHighestFinger(row);
    if (highestFinger) {
      highestFinger.updateParams();
      if (!voice.isOn) {
        voice.noteOn(pad.note, 127);
      }
    } else {
      voice.noteOff(pad.note, 127);
    }
  }

  getHighestFinger(row: number) {
    let highest: StringFinger | null = null;
    for (const f of fingersById.values()) {
      if (!(f instanceof StringFinger) || f.pad?.row !== row) {
        continue;
      }
      if (!highest || f.x > highest.x) {
        highest = f;
      }
    }
    return highest;
  }

  updateParams() {
    if (!this.pad || !this.voice) {
      throw new Error('invariant violated!!!');
    }
    if (this !== this.getHighestFinger(this.pad.row)) {
      return;
    }
    this.updateGlide();
    this.voice!.slide = clamp((this.pad.centerY - this.y) / (padHeight / 2), -1, 1);
    this.voice!.pressure = this.pressure;
  }

  updateGlide() {
    if (this.pad && this === this.getHighestFinger(this.pad.row)) {
      super.updateGlide();
    }
  }

  lift() {
    if (this.pad) {
      this.updateRow(this.pad.row);
    }
  }

  render() {
    renderMpeFinger(this.pad, this.voice);
  }
}

function firstPadIn(row: number) {
  return pads.find((pad) => pad.row === row && pad.col === 0)!;
}

const ENABLE_STRUM_BEND = true;
const STRUM_BEND_FACTOR = 0.5;

class FretFinger extends Finger {
  pressure = 0;

  move(x: number, y: number, pressure: number) {
    if (!this.pad) {
      this.pad = pads.find((pad) => pad.col < 12 && pad.contains(x, y)) ?? null;
    }

    this.x = x;
    this.y = y;
    this.pressure = pressure;
    this.updateGlide();
  }

  updateGlide() {
    if (this.pad) {
      const highestFinger = this.getHighestFinger(this.pad.row)!;
      highestFinger?._updateGlide();
    }
  }

  _updateGlide() {
    const { row } = this.pad!;
    const soundingPad = firstPadIn(row);
    const bend = ENABLE_STRUM_BEND
      ? (Math.abs(this.y - this.y0) / padHeight) * STRUM_BEND_FACTOR
      : 0;
    const voice = this.synth.voices[row];
    const nearestFret = this.getNearestFret();
    voice.glide = nearestFret.note + bend - soundingPad.note;
  }

  getHighestFinger(row: number) {
    let highest: FretFinger | null = null;
    for (const f of fingersById.values()) {
      if (!(f instanceof FretFinger) || f.pad?.row !== row) {
        continue;
      }
      if (!highest || f.x > highest.x) {
        highest = f;
      }
    }
    return highest;
  }

  getNearestFret() {
    let nearestFret = this.pad!;
    let nearestPadDist = Math.abs(nearestFret.centerX - this.x);
    for (const pad of pads) {
      if (pad.row !== this.pad!.row || pad.col >= LEFTMOST_STRUM_COL) {
        continue;
      }
      const d = Math.abs(pad.centerX - this.x);
      if (d < nearestPadDist) {
        nearestFret = pad;
        nearestPadDist = d;
      }
    }
    return nearestFret;
  }

  lift() {
    this.updateGlide();
    if (this.pad && !this.getHighestFinger(this.pad.row)) {
      const voice = this.synth.voices[this.pad.row];
      const soundingPad = firstPadIn(this.pad.row);
      voice.noteOff(soundingPad.note, 127);
    }
  }

  render() {
    if (!this.pad) {
      return;
    }

    const myPad = this.getNearestFret();
    const myX = myPad.centerX;
    const myY = this.y - this.y0 + myPad.centerY;

    const color = `rgba(100, 100, 100, 0.5)`;
    ctx.beginPath();
    ctx.fillStyle = color;
    ctx.arc(myX, myY, padWidth / 3, 0, Math.PI * 2);
    ctx.fill();

    for (let pad of pads) {
      if (pad.col !== 0 || pad.row === myPad.row) {
        continue;
      }

      const col = myPad.note - pad.note;
      if (col < 0 || col >= LEFTMOST_STRUM_COL) {
        continue;
      }

      const x = pad.centerX + col * padWidth;
      const y = myY + pad.y - myPad.y;
      ctx.beginPath();
      ctx.strokeStyle = color;
      ctx.arc(x, y, padWidth / 3, 0, Math.PI * 2);
      ctx.stroke();
    }
  }
}

class PickFinger extends Finger {
  // TODO: b/c move() gets called in superclass's constructor, these initializations don't get done
  // until after the 1st call to move()
  velocity = 1;
  maxVelocity = 0;
  prev?: { t: number; y: number };

  move(x: number, y: number, pressure: number) {
    const t = performance.now();
    const numStrumCols = NUM_COLS - LEFTMOST_STRUM_COL;
    this.x = innerWidth - MARGIN - (numStrumCols * padWidth) / 2;
    this.y = y;

    // update velocity
    if (this.prev === undefined) {
      this.prev = { t: performance.now(), y };
      this.velocity = this.maxVelocity = 0.5;
    } else {
      this.velocity = Math.min((15 * Math.abs(y - this.prev.y)) / (t - this.prev.t) / padHeight, 1);
    }
    this.maxVelocity = Math.max(this.velocity, this.maxVelocity);
    // status = `${this.velocity.toFixed(4)} / ${this.maxVelocity.toFixed(4)}`;
    this.prev.t = t;
    this.prev.y = y;

    const oldPad = this.pad;
    this.pad = pads.find((pad) => pad.col === 12 && pad.contains(this.x, this.y)) ?? null;
    const changedPads = this.pad !== oldPad;
    if (!changedPads || !this.pad) {
      return;
    }

    const { row, note } = firstPadIn(this.pad.row);
    const voice = synth.voices[row];
    const f = [...fingersById.values()].find((f) => f instanceof FretFinger && f.pad?.row === row);
    if (f) {
      (f as FretFinger).updateGlide();
      voice.noteOn(note, 127 * this.velocity);
    }
  }

  lift() {
    // no op
  }

  render() {
    const color = `rgba(100, 100, 100, 0.5)`;
    ctx.beginPath();
    ctx.fillStyle = color;
    ctx.arc(this.x, this.y, padWidth / 3, 0, Math.PI * 2);
    ctx.fill();
  }
}

function renderMpeFinger(fingerPad: Pad | null, voice: Voice | null) {
  if (!fingerPad || !voice) {
    return;
  }

  for (let pad of pads) {
    if (pad.col !== 0) {
      continue;
    }

    const x = pad.centerX + (fingerPad.note - pad.note + voice.glide) * padWidth;
    const y = pad.centerY - voice.slide * (padHeight / 2);
    const color = `rgba(100, 100, 100, ${voice.pressure})`;

    const shouldFill = pad.row === fingerPad.row;
    if (shouldFill) {
      ctx.beginPath();
      ctx.arc(x, y, padWidth / 4, 0, Math.PI * 2);
      ctx.fillStyle = color;
      ctx.fill();
    }

    for (let idx = 0; idx < 4; idx++) {
      ctx.beginPath();
      ctx.strokeStyle = color;
      const offset = (performance.now() / 2000 + idx / 4) * TAU;
      ctx.arc(x, y, padWidth / 3, offset, offset + TAU / 8);
      ctx.stroke();
    }
  }
}

// ---- other UI stuff -----

abstract class Thing {
  pointer: { id: number; x0: number; y0: number } | null = null;

  constructor(readonly thingAbove?: Thing) { }

  get x(): number {
    if (this.thingAbove) {
      return this.thingAbove.x;
    } else {
      throw new Error('must override x if no thingAbove');
    }
  }

  get centerX() {
    return this.x + this.w / 2;
  }

  get rightX() {
    return this.x + this.w;
  }

  get y(): number {
    if (this.thingAbove) {
      return this.thingAbove.bottomY;
    } else {
      throw new Error('must override y if no thingAbove');
    }
  }

  get centerY() {
    return this.y + this.h / 2;
  }

  get bottomY() {
    return this.y + this.h;
  }

  get w(): number {
    return this.thingAbove?.w ?? buttonWidth;
  }

  get h(): number {
    return this.thingAbove?.h ?? buttonHeight;
  }

  contains(px: number, py: number) {
    return this.x <= px && px <= this.rightX && this.y <= py && py <= this.bottomY;
  }

  get backgroundColor(): string {
    return this.pointer ? '#ccc' : '#ddd';
  }

  get foregroundColor(): string {
    return TEXT_COLOR;
  }

  render(label?: string) {
    ctx.beginPath();
    ctx.lineWidth = 3;
    ctx.strokeStyle = '#eee';
    ctx.roundRect(this.x, this.y, this.w, this.h, RECTANGLE_RADIUS);
    ctx.stroke();

    ctx.beginPath();
    ctx.fillStyle = this.backgroundColor;
    ctx.roundRect(this.x, this.y, this.w, this.h, RECTANGLE_RADIUS);
    ctx.fill();

    if (label) {
      this.renderLabel(label);
    }
  }

  renderOnTop() {
    // override if you need it!
  }

  renderLabel(label: string) {
    ctx.beginPath();
    ctx.font = BUTTON_FONT;
    ctx.fillStyle = this.foregroundColor;
    ctx.fillText(label, this.x + (this.w - ctx.measureText(label).width) / 2, this.y + this.h / 2);
    ctx.fill();
  }

  onPointerDown(x: number, y: number) {
    ensureInitialized();
  }

  onPointerMove(x: number, y: number) { }

  onPointerUp(x: number, y: number): void { }
}

class ParamJoystick extends Thing {
  constructor(
    readonly synth: Synth,
    readonly xParamNum: number,
    readonly yParamNum: number,
    public name: string,
    readonly thingAboveOrGetX: Thing | (() => number),
  ) {
    super(thingAboveOrGetX instanceof Thing ? thingAboveOrGetX : undefined);
  }

  get x() {
    return this.thingAboveOrGetX instanceof Thing ? super.x : this.thingAboveOrGetX();
  }

  get y() {
    return this.thingAboveOrGetX instanceof Thing ? super.y : MARGIN;
  }

  get w() {
    return (buttonWidth * 2) / 3;
  }

  get h() {
    return buttonHeight;
  }

  get foregroundColor() {
    return synth === this.synth ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }

  get backgroundColor() {
    return synth === this.synth ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
  }

  readonly radius = 8;

  render() {
    super.render();

    ctx.globalAlpha = 0.2;
    ctx.fillStyle = synth === this.synth ? DARK_BACKGROUND_COLOR_ACTIVE : DARK_BACKGROUND_COLOR;
    ctx.beginPath();
    ctx.arc(
      this.x + this.radius + this.xValue * (this.w - 2 * this.radius),
      this.bottomY - this.radius - this.yValue * (this.h - 2 * this.radius),
      this.radius,
      0,
      TAU,
    );
    ctx.fill();
    ctx.globalAlpha = 1;

    // ctx.fillStyle = synth === this.synth ? DARK_BACKGROUND_COLOR_ACTIVE : DARK_BACKGROUND_COLOR;
    ctx.beginPath();
    ctx.arc(
      this.x + this.radius + this.xValue * (this.w - 2 * this.radius),
      this.bottomY - this.radius - this.yValue * (this.h - 2 * this.radius),
      this.radius / 2,
      0,
      TAU,
    );
    ctx.fill();

    ctx.font = SELECTOR_FONT;
    ctx.fillStyle = synth === this.synth ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
    ctx.beginPath();
    ctx.fillText(
      this.name,
      this.centerX - ctx.measureText(this.name).width / 2,
      this.centerY + SELECTOR_LINE_HEIGHT / 2 - SELECTOR_BASELINE_ADJUSTMENT,
    );
    ctx.fill();
  }

  get xValue() {
    return this.synth?.voices[0]?.params[this.xParamNum] ?? 0;
  }

  set xValue(newValue: number) {
    this.synth.setParam(this.xParamNum, newValue);
  }

  get yValue() {
    return this.synth?.voices[0]?.params[this.yParamNum] ?? 0;
  }

  set yValue(newValue: number) {
    this.synth.setParam(this.yParamNum, newValue);
  }

  pos0: Position | null = null;
  value0: Position | null = null;

  onPointerDown(x: number, y: number) {
    ensureInitialized();
    this.pos0 = { x, y };
    this.value0 = { x: this.xValue, y: this.yValue };
  }

  onPointerMove(x: number, y: number) {
    const xv = clamp(this.value0!.x + (x - this.pos0!.x) / (this.w - this.radius * 2), 0, 1);
    const yv = clamp(this.value0!.y - (y - this.pos0!.y) / (this.h - this.radius * 2), 0, 1);
    this.xValue = xv;
    this.yValue = yv;
    showStatus(`${this.name} = (${xv.toFixed(3)}, ${yv.toFixed(3)})`);
  }
}

class ParamKnob extends Thing {
  constructor(
    readonly synth: Synth,
    readonly paramNum: number,
    public name: string,
    readonly thingAboveOrGetX: Thing | (() => number),
  ) {
    super(thingAboveOrGetX instanceof Thing ? thingAboveOrGetX : undefined);
  }

  get x() {
    return this.thingAboveOrGetX instanceof Thing ? super.x : this.thingAboveOrGetX();
  }

  get y() {
    return this.thingAboveOrGetX instanceof Thing ? super.y : MARGIN;
  }

  get w() {
    return (buttonWidth * 2) / 3;
  }

  get h() {
    return buttonHeight;
  }

  get foregroundColor() {
    return synth === this.synth ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }

  get backgroundColor() {
    return synth === this.synth ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
  }

  render() {
    super.render();

    const radius = 0.3 * Math.min(this.w, this.h);

    ctx.globalAlpha = 0.2;
    ctx.beginPath();
    ctx.fillStyle = synth === this.synth ? DARK_BACKGROUND_COLOR_ACTIVE : DARK_BACKGROUND_COLOR;
    ctx.arc(this.centerX, this.centerY, radius, 0, TAU);
    ctx.lineTo(this.centerX, this.centerY);
    ctx.fill();
    ctx.globalAlpha = 1;

    ctx.beginPath();
    ctx.arc(this.centerX, this.centerY, radius, TAU / 4, TAU * this.value + TAU / 4);
    ctx.lineTo(this.centerX, this.centerY);
    ctx.fill();

    ctx.beginPath();
    ctx.fillStyle = synth === this.synth ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
    ctx.arc(this.centerX, this.centerY, radius - 5, 0, TAU);
    ctx.lineTo(this.centerX, this.centerY);
    ctx.fill();

    ctx.font = SELECTOR_FONT;
    ctx.fillStyle = synth === this.synth ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
    ctx.beginPath();
    ctx.fillText(
      this.name,
      this.centerX - ctx.measureText(this.name).width / 2,
      this.centerY + SELECTOR_LINE_HEIGHT / 2 - SELECTOR_BASELINE_ADJUSTMENT,
    );
    ctx.fill();
  }

  get value() {
    return this.synth?.voices[0]?.params[this.paramNum] ?? 0;
  }

  set value(newValue: number) {
    this.synth.setParam(this.paramNum, newValue);
  }

  value0: number | null = null;
  prevAngle: number | null = null;

  onPointerDown(x: number, y: number) {
    ensureInitialized();
    this.value0 = this.value;
    this.prevAngle = Math.atan2(y - this.centerY, x - this.centerX);
  }

  onPointerMove(x: number, y: number) {
    const newAngle = Math.atan2(y - this.centerY, x - this.centerX);
    const diff = ((((newAngle - this.prevAngle! + Math.PI) % TAU) + TAU) % TAU) - Math.PI;
    this.value = clamp(this.value + diff / TAU, 0, 1);
    this.prevAngle = newAngle;
  }
}

class SelectorThing<T> extends Thing {
  readonly optionLabels: string[];

  constructor(
    readonly thingAboveOrGetX: Thing | (() => number),
    readonly options: T[],
    readonly getValue: () => T,
    readonly setValue: (value: T) => void,
    readonly valueToString: (value: T) => string,
    readonly placeCurrentValueAtY0 = false,
  ) {
    super(thingAboveOrGetX instanceof Thing ? thingAboveOrGetX : undefined);
    this.optionLabels = options.map(valueToString);
    if (!placeCurrentValueAtY0) {
      this.optionLabels.unshift('');
    }
  }

  get x() {
    return this.thingAboveOrGetX instanceof Thing ? super.x : this.thingAboveOrGetX();
  }

  get y() {
    return this.thingAboveOrGetX instanceof Thing ? super.y : MARGIN;
  }

  pointerPos?: { initial: Position; current: Position };
  popUpWidth = 0;
  popUpHeight = 0;
  popUpX = 0;
  popUpY = 0;
  optionX = 0;
  optionY0 = 0;
  maxLabelWidth = 0;

  onPointerDown(x: number, y: number) {
    this.pointerPos = { initial: { x, y }, current: { x, y } };

    ctx.font = SELECTOR_FONT;
    this.maxLabelWidth = this.optionLabels
      .map((label) => ctx.measureText(label).width)
      .reduce((a, b) => Math.max(a, b));
    this.popUpWidth = 1.125 * SELECTOR_FINGER_WIDTH + this.maxLabelWidth;
    this.popUpHeight = (this.optionLabels.length + 0.5) * SELECTOR_LINE_HEIGHT;

    const goRight = this.pointerPos.initial.x < innerWidth / 2;
    this.popUpX = goRight
      ? this.pointerPos.initial.x - SELECTOR_FINGER_WIDTH / 2
      : this.pointerPos.initial.x - this.popUpWidth + SELECTOR_FINGER_WIDTH / 2;
    this.popUpY = this.pointerPos.initial.y - SELECTOR_LINE_HEIGHT;
    if (this.placeCurrentValueAtY0) {
      this.popUpY -= SELECTOR_LINE_HEIGHT * this.options.indexOf(this.getValue());
    }

    this.optionX = this.popUpX + (goRight ? SELECTOR_FINGER_WIDTH : 0.125 * SELECTOR_FINGER_WIDTH);
    this.optionY0 = this.popUpY + (this.placeCurrentValueAtY0 ? 0 : SELECTOR_LINE_HEIGHT);

    this.onPointerMove(x, y);
  }

  onPointerMove(x: number, y: number) {
    this.pointerPos!.current.x = x;
    this.pointerPos!.current.y = y;
    const firstOptionY = this.popUpY + (this.placeCurrentValueAtY0 ? 1 : 2) * SELECTOR_LINE_HEIGHT;
    const selectedOptionIdx = Math.round((y - firstOptionY) / SELECTOR_LINE_HEIGHT);
    if (selectedOptionIdx < 0) {
      return;
    }
    const selectedOption = this.options[Math.min(selectedOptionIdx, this.options.length - 1)];
    if (selectedOption !== this.getValue()) {
      this.setValue(selectedOption);
    }
  }

  onPointerUp(x: number, y: number): void {
    this.pointerPos = undefined;
  }

  render() {
    super.render(this.valueToString(this.getValue()));
  }

  renderOnTop() {
    if (!this.pointerPos) {
      return;
    }

    ctx.fillStyle = 'rgba(255, 255, 255, 0.5)';
    ctx.filter = 'drop-shadow(5px 5px 5px)';
    ctx.beginPath();
    ctx.roundRect(this.popUpX, this.popUpY, this.popUpWidth, this.popUpHeight, RECTANGLE_RADIUS);
    ctx.fill();
    ctx.filter = 'none';

    ctx.filter = 'blur(10px)';
    ctx.fillStyle = 'rgba(255, 255, 255, 0.8)';
    ctx.beginPath();
    ctx.roundRect(this.popUpX, this.popUpY, this.popUpWidth, this.popUpHeight, RECTANGLE_RADIUS);
    ctx.fill();
    ctx.filter = 'none';

    let optionY = this.optionY0;
    for (const value of this.options) {
      optionY += SELECTOR_LINE_HEIGHT;
      this.renderOption(
        this.valueToString(value),
        this.getValue() === value,
        this.optionX,
        optionY,
      );
    }
  }

  renderOption(label: string, isCurrent: boolean, x: number, y: number) {
    ctx.font = SELECTOR_FONT;

    if (isCurrent) {
      ctx.filter = 'blur(10px)';
      ctx.beginPath();
      ctx.fillStyle = LIGHT_BACKGROUND_COLOR_ACTIVE;
      ctx.roundRect(
        x - EMBEDDED_SELECTOR_PADDING,
        y - SELECTOR_LINE_HEIGHT + SELECTOR_BASELINE_ADJUSTMENT,
        this.maxLabelWidth + 2 * EMBEDDED_SELECTOR_PADDING,
        SELECTOR_LINE_HEIGHT,
        RECTANGLE_RADIUS * 2,
      );
      ctx.fill();
      ctx.filter = 'none';
    }

    ctx.beginPath();
    ctx.fillStyle = isCurrent ? '#333' : '#999';
    ctx.fillText(label, x, y);
    ctx.fill();
  }
}

class EmbeddedSelectorThing<T> extends SelectorThing<T> {
  constructor(
    readonly parent: Thing,
    readonly vPlacement: 'top' | 'bottom',
    readonly hPlacement: 'left' | 'right',
    options: T[],
    getValue: () => T,
    setValue: (value: T) => void,
    valueToString: (value: T) => string,
    readonly isActive: () => boolean,
    placeCurrentValueAtY0 = false,
  ) {
    super(() => 0, options, getValue, setValue, valueToString, placeCurrentValueAtY0);
  }

  get x() {
    return this.hPlacement === 'left'
      ? this.parent.x + EMBEDDED_SELECTOR_PADDING
      : this.parent.rightX - this.w - EMBEDDED_SELECTOR_PADDING;
  }

  get y() {
    return this.vPlacement === 'top'
      ? this.parent.y + EMBEDDED_SELECTOR_PADDING
      : this.parent.bottomY - this.h - EMBEDDED_SELECTOR_PADDING + SELECTOR_BASELINE_ADJUSTMENT;
  }

  get w() {
    ctx.font = SELECTOR_FONT;
    return ctx.measureText(this.valueToPaddedString(this.getValue())).width;
  }

  get h() {
    return SELECTOR_LINE_HEIGHT;
  }

  render() {
    this.renderLabel(this.valueToPaddedString(this.getValue()));
  }

  valueToPaddedString(value: T) {
    return this.hPlacement === 'left'
      ? this.valueToString(value) + '    '
      : '    ' + this.valueToString(value);
  }

  get foregroundColor() {
    return this.isActive() ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }
}

abstract class Oscilloscope extends Thing {
  static gradient = ctx.createLinearGradient(0, 0, 0, canvas.height);
  static {
    this.gradient.addColorStop(0, 'rgba(0, 255, 255, 0.8)'); // Cyan at top
    this.gradient.addColorStop(1, 'rgba(0, 0, 128, 0.2)'); // Faded navy at bottom
  }

  constructor(readonly synth: Synth) {
    super();
  }

  abstract get x(): number;

  get y() {
    return MARGIN;
  }

  get w() {
    return padWidth * 4;
  }

  get h() {
    return buttonHeight * 3;
  }

  pointerDownY: number | null = null;
  onPointerDown(x: number, y: number) {
    this.synth.makeActive();
    this.pointerDownY = y;
  }

  onPointerMove(x: number, y: number) {
    const dy = y - this.pointerDownY!;
    if (editingPatch) {
      padRowOffset =
        -EDITOR_HEIGHT_IN_PADS + (dy > 0 ? Math.min(dy / padHeight, EDITOR_HEIGHT_IN_PADS) : 0);
    } else {
      padRowOffset = dy < 0 ? Math.max(dy / padHeight, -EDITOR_HEIGHT_IN_PADS) : 0;
    }
  }

  onPointerUp(x: number, y: number) {
    if (editingPatch) {
      if (padRowOffset > 1 - EDITOR_HEIGHT_IN_PADS) {
        editingPatch = false;
        padRowOffset = 0;
      } else {
        padRowOffset = -EDITOR_HEIGHT_IN_PADS;
      }
    } else {
      if (padRowOffset <= -1) {
        editingPatch = true;
        padRowOffset = -EDITOR_HEIGHT_IN_PADS;
      } else {
        padRowOffset = 0;
      }
    }
    this.pointerDownY = null;
  }

  render() {
    super.render();
    const data = this.synth.getTimeDomainData();
    if (!data) {
      return;
    }

    const freq = this.synth.getLowestNoteFreq();
    const samplesPerCycle = SAMPLE_RATE / freq;
    const cyclesToDisplay = 2;
    const requiredSamples = Math.round(samplesPerCycle * cyclesToDisplay);

    // Find the first upward zero-crossing point
    let triggerIndex = 0;
    for (let i = 0; i < data.length - 1; i++) {
      // Center line is 128
      // We look for a value <= 128 followed immediately by a value > 128
      if (data[i] <= 128 && data[i + 1] > 128) {
        triggerIndex = i;
        break; // Found our starting point
      }
    }

    ctx.beginPath();

    const numSamplesToShow = Math.min(requiredSamples, data.length - triggerIndex);
    const sliceWidth = this.w / numSamplesToShow;
    let x = this.x;
    let y = 0;
    // let firstY = 0;

    // Start iteration from the trigger point
    for (let i = triggerIndex; i < numSamplesToShow; i++) {
      const v = data[i] / 128;
      y = (v * this.h) / 2;
      if (i === triggerIndex) {
        ctx.moveTo(x, y);
        // firstY = y;
      } else {
        ctx.lineTo(x, y);
      }
      x += sliceWidth;
    }

    // ctx.lineTo(this.rightX, y);
    // ctx.lineTo(this.rightX, this.bottomY);
    // ctx.lineTo(this.x, this.bottomY);
    // ctx.lineTo(this.x, firstY);

    // ctx.fillStyle = Oscilloscope.gradient;
    // ctx.fill();

    ctx.lineWidth = 1;
    ctx.strokeStyle = synth === this.synth ? LINE_COLOR_ACTIVE : LINE_COLOR;
    ctx.stroke();
  }

  get foregroundColor() {
    return synth === this.synth ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }

  get backgroundColor() {
    return synth === this.synth ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
  }
}

const synth1Oscilloscope = new (class extends Oscilloscope {
  get x() {
    return MARGIN;
  }
})(synth1);
const synth1Controls = synth1.controls(synth1Oscilloscope);

const synth2Oscilloscope = new (class extends Oscilloscope {
  get x() {
    return synth1Controls.at(-1)!.rightX;
  }
})(synth2);
const synth2Controls = synth2.controls(synth2Oscilloscope);

let tempo = 120;

const tempoScrubber = new (class extends Thing {
  get x() {
    return innerWidth - MARGIN - this.w;
  }

  get y() {
    return MARGIN;
  }

  get w() {
    return padWidth * 3;
  }

  render() {
    super.render(`tempo ${tempo}`);
  }

  tempo0: number | null = null;

  onPointerDown(x: number, y: number) {
    this.tempo0 = tempo;
  }

  onPointerMove(x: number, y: number) {
    const dy = y - this.pointer!.y0;
    tempo = clamp(Math.round(this.tempo0! - dy / 10), 10, 200);
    synth2.sendToWorklet({ command: 'set tempo', value: tempo });
  }

  get foregroundColor() {
    return synth === synth2 ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }

  get backgroundColor() {
    return synth === synth2 ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
  }
})();

let stepsRecorded: number[][] = [];
let seqStatus: 'recording' | 'stopped' | 'playing';
let currentStep = 0;

const stepsButton = new (class extends Thing {
  render() {
    super.render(
      seqStatus === 'playing'
        ? `step ${currentStep + 1} / ${stepsRecorded.length}`
        : stepsRecorded.length === 0
          ? 'record steps'
          : `${stepsRecorded.length} steps`,
    );
  }

  onPointerDown() {
    stopSequencer();
    stepsRecorded = [];
    seqStatus = 'recording';
  }

  onPointerUp() {
    synth2.sendToWorklet({ command: 'load steps', steps: stepsRecorded });
    if (stepsRecorded.length > 0) {
      startSequencer();
    } else {
      stopSequencer();
    }
  }

  get foregroundColor() {
    return synth === synth2 ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }

  get backgroundColor() {
    return synth === synth2 ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
  }
})(tempoScrubber);

const playButton = new (class extends Thing {
  render() {
    super.render(
      seqStatus === 'recording' ? 'add rest' : seqStatus === 'playing' ? 'stop' : 'play',
    );
  }

  onPointerDown() {
    if (seqStatus === 'recording') {
      stepsRecorded.push([]);
    } else if (seqStatus === 'playing') {
      stopSequencer();
    } else {
      startSequencer();
    }
  }

  get foregroundColor() {
    return synth === synth2 ? TEXT_COLOR_ACTIVE : TEXT_COLOR;
  }

  get backgroundColor() {
    return synth === synth2 ? LIGHT_BACKGROUND_COLOR_ACTIVE : LIGHT_BACKGROUND_COLOR;
  }
})(stepsButton);

function startSequencer() {
  if (stepsRecorded.length === 0) {
    return;
  }

  synth2.setParam(0, 0); // no glide
  seqStatus = 'playing';
  currentStep = 0;
  synth2.sendToWorklet({ command: 'start sequencer' });
}

function stopSequencer() {
  seqStatus = 'stopped';
  synth2.sendToWorklet({ command: 'stop sequencer' });
  for (const pad of pads) {
    pad.isBeingPlayedBySequencer = false;
  }
}

const seqControls = [tempoScrubber, stepsButton, playButton];

const things: Thing[] = [
  synth1Oscilloscope,
  ...synth1Controls,
  synth2Oscilloscope,
  ...synth2Controls,
  ...seqControls,
];

/** Pointer position in canvas-relative coordinates (same space as pads and things). */
function canvasCoords(e: PointerEvent): { x: number; y: number } {
  return { x: e.offsetX, y: e.offsetY };
}

canvas.addEventListener('pointerdown', (e) => {
  ensureInitialized();
  const { x, y } = canvasCoords(e);

  let hit = false;
  for (let idx = things.length - 1; !hit && idx >= 0; idx--) {
    const thing = things[idx];
    if (thing.contains(x, y)) {
      thing.onPointerDown(x, y);
      thing.pointer = { id: e.pointerId, x0: x, y0: y };
      e.preventDefault();
      hit = true;
    }
  }

  if (!hit) {
    pointerDownForPads(e.pointerId, x, y, getPressure(e));
  }

  if (hit || fingersById.has(e.pointerId)) {
    canvas.setPointerCapture(e.pointerId);
  }
});

canvas.addEventListener('pointermove', (e) => {
  const { x, y } = canvasCoords(e);

  for (const thing of things) {
    if (thing.pointer?.id === e.pointerId) {
      thing.onPointerMove(x, y);
      e.preventDefault();
      break;
    }
  }

  const finger = fingersById.get(e.pointerId);
  if (finger) {
    finger.move(x, y, getPressure(e));
    e.preventDefault();
  }
});

canvas.addEventListener('pointerup', (e) => {
  const { x, y } = canvasCoords(e);

  for (const thing of things) {
    if (thing.pointer?.id === e.pointerId) {
      thing.onPointerUp(x, y);
      thing.pointer = null;
      e.preventDefault();
      break;
    }
  }

  const finger = fingersById.get(e.pointerId);
  if (finger) {
    fingersById.delete(e.pointerId);
    finger.lift();
  }
});

canvas.addEventListener('pointercancel', (e) => {
  for (const thing of things) {
    if (thing.pointer?.id === e.pointerId) {
      thing.pointer = null;
      break;
    }
  }
  const finger = fingersById.get(e.pointerId);
  if (finger) {
    fingersById.delete(e.pointerId);
    finger.lift();
  }
});

function render() {
  ctx.clearRect(0, 0, innerWidth, innerHeight);

  const padsBottomY = MARGIN + (FIRST_ROW + NUM_ROWS + padRowOffset) * padHeight;
  editor.container.style.top = `${padsBottomY}px`;
  editor.container.style.height = `${EDITOR_HEIGHT_IN_PADS * padHeight}px`;

  for (const pad of pads) {
    pad.render();
  }

  for (const finger of fingersById.values()) {
    finger.render();
  }

  for (const thing of things) {
    thing.render();
  }

  for (const thing of things) {
    thing.renderOnTop();
  }

  if (status.length > 0 && Date.now() - statusTime < STATUS_LINGER_TIME_MS) {
    ctx.font = '16px Inter';
    const { width } = ctx.measureText(status);
    ctx.beginPath();
    ctx.fillStyle = '#00ff';
    ctx.fillText(status, innerWidth - width - 20, innerHeight - 20);
    ctx.fill();
  }

  Finger.updateQuantizationOffsets();

  requestAnimationFrame(render);
}

render();

// ---- web audio -----

let webAudioState: 'need initialization' | 'initializing' | 'ready' = 'need initialization';

function ensureInitialized() {
  if (webAudioState !== 'need initialization') {
    return;
  }

  webAudioState = 'initializing';
  status = 'initializing audio...';
  context.resume();

  synth1.init();
  synth2.init();
  synth2.sendToWorklet({ command: 'load steps', steps: stepsRecorded });

  webAudioState = 'ready';

  showStatus('ready!');
}

function onMessageFromWorklet(msg: MessageFromWorklet) {
  switch (msg.event) {
    case 'seq step': {
      if (msg.action === 'noteOn') {
        currentStep = msg.index;
      }
      for (const note of stepsRecorded[msg.index] || []) {
        for (const pad of pads) {
          if (pad.note === note) {
            pad.isBeingPlayedBySequencer = msg.action === 'noteOn';
          }
        }
      }
      break;
    }
    case 'log': {
      showStatus(msg.message);
      break;
    }
    default:
      showStatus('??? ' + JSON.stringify(msg));
      break;
  }
}

// when the iPad is turned off then turned back on, the audio context gets suspended
// we need to resume it, and that only works when done in response to a user interaction
window.addEventListener('pointerdown', () => {
  if (context.state === 'suspended' || context.state === 'interrupted') {
    context.resume();
  }
});

// ----- interactions -----

function pointerDownForPads(pointerId: number, x: number, y: number, pressure: number) {
  const somePadsAreTouched = [...fingersById.values()].some((f) => !!f.pad);
  for (const pad of pads) {
    if (pad.contains(x, y)) {
      if (seqStatus === 'recording' && synth === synth2) {
        if (!somePadsAreTouched) {
          // this is the first note for this step!
          stepsRecorded.push([]);
        }
        stepsRecorded.at(-1)!.push(pad.note);
      }
      Finger.add(pointerId, x, y, pressure);
      break;
    }
  }
}

function getPressure(e: PointerEvent) {
  if (e.pointerType === 'pen') {
    return e.pressure;
  } else if (e.pointerType === 'touch') {
    return Math.abs(Math.max(e.width, e.height) - 20) / (150 - 20);
  } else {
    return 1;
  }
}
